import React from "react";
import "./style.css";

const Buttons = ({ tasks, hideDoneTasks }) => (

    <div className="buttons">
        {tasks.length > 0 && (
            <>
                <button className="buttons__button">
                    {hideDoneTasks ? "Poka≈º" : "Ukryj"} zrobione
                </button>
                <button className="buttons__button"
                    disabled={tasks.every(({ done }) => done)}>
                    Uko≈Ñcz wszystkie
                </button>
            </>
        )}
    </div>
);

export default Buttons;